'use client';

import React, { useEffect, useMemo, useState, useRef } from 'react';
import { Vote, VoteItem } from '@/types/interfaces';
import {
  getVoteStatus,
  formatRemainingTime,
  formatTimeUntilStart,
} from '@/components/server/utils';
import { formatVotePeriodWithTimeZone } from '@/utils/date';
import { VoteCard, VoteRankCard } from '..';
import { VoteTimer } from '../common/VoteTimer';
import { VoteSearch } from './VoteSearch';
import { VoteButton } from '../common/VoteButton';
import { Badge, Card } from '@/components/common';
import { useLanguageStore } from '@/stores/languageStore';
import { getLocalizedString } from '@/utils/api/strings';
import { getCdnImageUrl } from '@/utils/api/image';
import { useRequireAuth } from '@/hooks/useAuthGuard';

// Task 4ÏóêÏÑú ÎßåÎì† Î¶¨ÏñºÌÉÄÏûÑ Ïª¥Ìè¨ÎÑåÌä∏Îì§ import
import { useVoteRealtime } from '@/hooks/useVoteRealtime';
import { RealtimeStatus } from '@/components/client/vote/common/RealtimeStatus';
import OptimizedRealtimeVoteResults from '@/components/client/vote/list/OptimizedRealtimeVoteResults';
import { AnimatedCount } from '@/components/ui/animations/RealtimeAnimations';
import { motion, AnimatePresence } from 'framer-motion';

export interface RealtimeVoteDetailPresenterProps {
  vote: Vote;
  initialItems: VoteItem[];
  rewards?: any[];
  className?: string;
}

export function RealtimeVoteDetailPresenter({
  vote,
  initialItems,
  rewards = [],
  className,
}: RealtimeVoteDetailPresenterProps) {
  const { currentLanguage } = useLanguageStore();
  const { withAuth } = useRequireAuth({
    customLoginMessage: {
      title: 'Ìà¨ÌëúÌïòÎ†§Î©¥ Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§',
      description:
        'Ïù¥ Ìà¨ÌëúÏóê Ï∞∏Ïó¨ÌïòÎ†§Î©¥ Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§. Î°úÍ∑∏Ïù∏ÌïòÏãúÍ≤†ÏäµÎãàÍπå?',
    },
  });

  // Í∏∞Ï°¥ ÏÉÅÌÉúÎì§
  const [selectedItem, setSelectedItem] = useState<VoteItem | null>(null);
  const [searchQuery, setSearchQuery] = useState('');
  const [isVoting, setIsVoting] = useState(false);
  const [timeLeft, setTimeLeft] = useState<{
    days: number;
    hours: number;
    minutes: number;
    seconds: number;
  } | null>(null);
  const [showVoteModal, setShowVoteModal] = useState(false);
  const [voteCandidate, setVoteCandidate] = useState<VoteItem | null>(null);
  const [voteAmount, setVoteAmount] = useState(1);
  const [availableVotes, setAvailableVotes] = useState(10);
  const [headerHeight, setHeaderHeight] = useState(0);
  const headerRef = useRef<HTMLDivElement>(null);

  // Î¶¨ÏñºÌÉÄÏûÑ ÏÉÅÌÉú Í¥ÄÎ¶¨
  const [realtimeVoteItems, setRealtimeVoteItems] = useState<VoteItem[]>(initialItems);
  const [realtimeTotalVotes, setRealtimeTotalVotes] = useState<number>(
    initialItems.reduce((sum, item) => sum + (item.vote_total || 0), 0)
  );

  // Î¶¨ÏñºÌÉÄÏûÑ ÌõÖ ÏÇ¨Ïö©
  const {
    connectionStatus,
    isConnected,
    lastEvent,
    eventCount,
    connect,
    disconnect,
    cleanup
  } = useVoteRealtime({
    voteId: vote.id,
    autoConnect: true,
    onVoteUpdate: (event) => {
      console.log('Ïã§ÏãúÍ∞Ñ Ìà¨Ìëú ÏóÖÎç∞Ïù¥Ìä∏:', event);
      
      // Ïù¥Î≤§Ìä∏Ïóê Îî∞Îùº ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏
      if (event.type === 'vote_item_updated' && event.payload) {
        const voteItem = event.payload;
        
        setRealtimeVoteItems(prev => 
          prev.map(item => 
            item.id === voteItem.id 
              ? { ...item, vote_total: voteItem.vote_total }
              : item
          )
        );
        
        // Ï¥ù Ìà¨Ìëú Ïàò Ïû¨Í≥ÑÏÇ∞
        setRealtimeTotalVotes(prev => {
          const currentItem = realtimeVoteItems.find(item => item.id === voteItem.id);
          const oldTotal = currentItem?.vote_total || 0;
          return prev - oldTotal + (voteItem.vote_total || 0);
        });
      } else if (event.type === 'vote_pick_created' && event.payload) {
        // ÏÉàÎ°úÏö¥ Ìà¨ÌëúÍ∞Ä ÏÉùÏÑ±ÎêòÏóàÏùÑ ÎïåÏùò Ï≤òÎ¶¨
        const votePick = event.payload;
        console.log('ÏÉàÎ°úÏö¥ Ìà¨Ìëú ÏÉùÏÑ±:', votePick);
        // Ïó¨Í∏∞ÏÑúÎäî vote_itemÏùò Ï¥ùÌï©ÏùÑ Îã§Ïãú Í≥ÑÏÇ∞Ìï¥Ïïº ÌïòÏßÄÎßå
        // Ïã§Ï†úÎ°úÎäî Îç∞Ïù¥ÌÑ∞Î≤†Ïù¥Ïä§ Ìä∏Î¶¨Í±∞ÏóêÏÑú vote_item_updated Ïù¥Î≤§Ìä∏Í∞Ä Î∞úÏÉùÌï† Í≤ÉÏûÖÎãàÎã§.
      }
    },
    onConnectionStatusChange: (status) => {
      console.log('Ïó∞Í≤∞ ÏÉÅÌÉú Î≥ÄÍ≤Ω:', status);
    },
  });

  // Ïã§ÏãúÍ∞Ñ Îç∞Ïù¥ÌÑ∞ Ïö∞ÏÑ† ÏÇ¨Ïö©, ÏóÜÏúºÎ©¥ Ï¥àÍ∏∞ Îç∞Ïù¥ÌÑ∞ ÏÇ¨Ïö©
  const voteItems = realtimeVoteItems;
  
  const voteStatus = getVoteStatus(vote);
  const canVote = voteStatus === 'ongoing';

  // ÎÇ®ÏùÄ ÏãúÍ∞Ñ Í≥ÑÏÇ∞ Î∞è ÏóÖÎç∞Ïù¥Ìä∏
  useEffect(() => {
    if (!vote.stop_at || voteStatus !== 'ongoing') return;

    const updateTimer = () => {
      const now = new Date().getTime();
      const endTime = new Date(vote.stop_at!).getTime();
      const difference = endTime - now;

      if (difference > 0) {
        const days = Math.floor(difference / (1000 * 60 * 60 * 24));
        const hours = Math.floor(
          (difference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60),
        );
        const minutes = Math.floor(
          (difference % (1000 * 60 * 60)) / (1000 * 60),
        );
        const seconds = Math.floor((difference % (1000 * 60)) / 1000);

        setTimeLeft({ days, hours, minutes, seconds });
      } else {
        setTimeLeft({ days: 0, hours: 0, minutes: 0, seconds: 0 });
      }
    };

    updateTimer();
    const timer = setInterval(updateTimer, 1000);

    return () => clearInterval(timer);
  }, [vote.stop_at, voteStatus]);

  // Ïª¥Ìè¨ÎÑåÌä∏ Ïñ∏ÎßàÏö¥Ìä∏ Ïãú Ï†ïÎ¶¨
  useEffect(() => {
    return () => {
      cleanup();
    };
  }, [cleanup]);

  // Ìà¨Ìëú Í∏∞Í∞Ñ Ìè¨Îß∑ÌåÖ (ÏãúÍ∞ÑÎåÄ Ï†ïÎ≥¥ Ìè¨Ìï®)
  const formatVotePeriod = () => {
    if (!vote.start_at || !vote.stop_at) return '';

    // ÏÉàÎ°úÏö¥ ÏãúÍ∞ÑÎåÄ Ìè¨Îß∑ÌåÖ Ìï®Ïàò ÏÇ¨Ïö©
    return formatVotePeriodWithTimeZone(vote.start_at, vote.stop_at, currentLanguage);
  };

  // ÌÉÄÏù¥Î®∏ Î†åÎçîÎßÅ (Ïï†ÎãàÎ©îÏù¥ÏÖò Ï∂îÍ∞Ä)
  const renderTimer = () => {
    if (voteStatus !== 'ongoing' || !timeLeft) return null;

    const { days, hours, minutes, seconds } = timeLeft;
    const isExpired =
      days === 0 && hours === 0 && minutes === 0 && seconds === 0;

    if (isExpired) {
      return (
        <motion.div 
          className='flex items-center gap-2'
          initial={{ scale: 0.9 }}
          animate={{ scale: 1 }}
          transition={{ type: "spring", stiffness: 300 }}
        >
          <span className='text-xl'>üö´</span>
          <span className='text-sm md:text-base font-bold text-red-600'>
            ÎßàÍ∞ê
          </span>
        </motion.div>
      );
    }

    return (
      <motion.div 
        className='flex items-center gap-2'
        initial={{ opacity: 0, y: -10 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.3 }}
      >
        <span className='text-xl'>‚è±Ô∏è</span>
        <div className='flex items-center gap-1 text-xs sm:text-sm font-mono font-bold'>
          <motion.span 
            className='bg-blue-100 text-blue-800 px-1.5 py-0.5 rounded text-xs'
            key={`days-${days}`}
            initial={{ scale: 1.2 }}
            animate={{ scale: 1 }}
            transition={{ duration: 0.2 }}
          >
            <AnimatedCount value={days} suffix="Ïùº" />
          </motion.span>
          <span className='text-gray-400'>:</span>
          <motion.span 
            className='bg-blue-100 text-blue-800 px-1.5 py-0.5 rounded text-xs'
            key={`hours-${hours}`}
            initial={{ scale: 1.2 }}
            animate={{ scale: 1 }}
            transition={{ duration: 0.2 }}
          >
            <AnimatedCount value={hours} suffix="Ïãú" />
          </motion.span>
          <span className='text-gray-400'>:</span>
          <motion.span 
            className='bg-blue-100 text-blue-800 px-1.5 py-0.5 rounded text-xs'
            key={`minutes-${minutes}`}
            initial={{ scale: 1.2 }}
            animate={{ scale: 1 }}
            transition={{ duration: 0.2 }}
          >
            <AnimatedCount value={minutes} suffix="Î∂Ñ" />
          </motion.span>
          <span className='text-gray-400'>:</span>
          <motion.span 
            className='bg-red-100 text-red-800 px-1.5 py-0.5 rounded animate-pulse text-xs'
            key={`seconds-${seconds}`}
            initial={{ scale: 1.2 }}
            animate={{ scale: 1 }}
            transition={{ duration: 0.2 }}
          >
            <AnimatedCount value={seconds} suffix="Ï¥à" />
          </motion.span>
        </div>
      </motion.div>
    );
  };

  // Ìà¨Ìëú ÏïÑÏù¥ÌÖú Îû≠ÌÇπ Í≥ÑÏÇ∞ (Ïï†ÎãàÎ©îÏù¥ÏÖòÏùÑ ÏúÑÌïú Ïù¥Ï†Ñ ÏàúÏúÑ Ï∂îÏ†Å)
  const rankedVoteItems = useMemo(() => {
    if (!voteItems.length) return [];

    const sortedItems = [...voteItems].sort(
      (a, b) => (b.vote_total || 0) - (a.vote_total || 0),
    );

    let currentRank = 1;
    let currentScore = sortedItems[0]?.vote_total || 0;

    return sortedItems.map((item, index) => {
      if (index > 0 && currentScore !== (item.vote_total || 0)) {
        currentRank = index + 1;
        currentScore = item.vote_total || 0;
      }

      return {
        ...item,
        rank: currentRank,
      };
    });
  }, [voteItems]);

  // Í≤ÄÏÉâ ÌïÑÌÑ∞ÎßÅ
  const filteredItems = useMemo(() => {
    if (!searchQuery) return rankedVoteItems;

    return rankedVoteItems.filter((item) => {
      const artistName = item.artist?.name
        ? getLocalizedString(
            item.artist.name,
            currentLanguage,
          )?.toLowerCase() || ''
        : '';
      const query = searchQuery.toLowerCase();
      return artistName.includes(query);
    });
  }, [rankedVoteItems, searchQuery, currentLanguage]);

  // Ìà¨Ìëú ÌôïÏù∏ ÌåùÏóÖ Ïó¥Í∏∞
  const handleCardClick = async (item: VoteItem) => {
    if (!canVote) return;

    await withAuth(() => {
      setVoteCandidate(item);
      setShowVoteModal(true);
    });
  };

  // Ìà¨Ìëú Ïã§Ìñâ
  const confirmVote = async () => {
    if (!voteCandidate || !canVote || isVoting) return;

    setIsVoting(true);
    try {
      // TODO: Ïã§Ï†ú Ìà¨Ìëú API Ìò∏Ï∂ú
      console.log('Ìà¨Ìëú Ïã§Ìñâ:', {
        voteId: vote.id,
        voteItemId: voteCandidate.id,
        amount: voteAmount,
      });

      // ÏûÑÏãú: Î°úÏª¨ ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏ (Ïã§Ï†úÎ°úÎäî Î¶¨ÏñºÌÉÄÏûÑÏúºÎ°ú ÏóÖÎç∞Ïù¥Ìä∏Îê®)
      // setVoteItems(prev => 
      //   prev.map(item => 
      //     item.id === voteCandidate.id 
      //       ? { ...item, vote_total: (item.vote_total || 0) + voteAmount }
      //       : item
      //   )
      // );

      setAvailableVotes(prev => Math.max(0, prev - voteAmount));
      setShowVoteModal(false);
      setVoteCandidate(null);
      setVoteAmount(1);
    } catch (error) {
      console.error('Ìà¨Ìëú Ïã§Ìñâ Ï§ë Ïò§Î•ò:', error);
      // TODO: ÏóêÎü¨ Ï≤òÎ¶¨
    } finally {
      setIsVoting(false);
    }
  };

  // Ìà¨Ìëú Ï∑®ÏÜå
  const cancelVote = () => {
    setShowVoteModal(false);
    setVoteCandidate(null);
    setVoteAmount(1);
  };

  // Ìó§Îçî ÎÜíÏù¥ Í≥ÑÏÇ∞
  useEffect(() => {
    const updateHeaderHeight = () => {
      if (headerRef.current) {
        setHeaderHeight(headerRef.current.offsetHeight);
      }
    };

    updateHeaderHeight();
    window.addEventListener('resize', updateHeaderHeight);
    return () => window.removeEventListener('resize', updateHeaderHeight);
  }, []);

  // Ìà¨Ìëú Ï†úÎ™© Í∞ÄÏ†∏Ïò§Í∏∞
  const voteTitle = useMemo(() => {
    if (!vote.title) return 'Ìà¨Ìëú';
    
    if (typeof vote.title === 'string') {
      return vote.title;
    }
    
    return getLocalizedString(vote.title, currentLanguage) || 'Ìà¨Ìëú';
  }, [vote.title, currentLanguage]);

  // Ï¥ù Ìà¨Ìëú Ïàò Í≥ÑÏÇ∞
  const totalVotes = useMemo(() => {
    return realtimeTotalVotes || voteItems.reduce((sum, item) => sum + (item.vote_total || 0), 0);
  }, [realtimeTotalVotes, voteItems]);

  // Í≤ÄÏÉâ Ìï∏Îì§Îü¨
  const handleSearch = (query: string) => {
    setSearchQuery(query);
  };

  return (
    <div className={`max-w-6xl mx-auto p-4 space-y-6 ${className}`}>
      {/* ÏÉÅÎã® Ìó§Îçî - Í≥†Ï†ï */}
      <motion.div 
        ref={headerRef}
        className="sticky top-0 z-10 bg-white border-b border-gray-200 pb-4"
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        {/* Ìà¨Ìëú Í∏∞Î≥∏ Ï†ïÎ≥¥ */}
        <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-4 mb-4">
          <div className="flex-1">
            <div className="flex items-center gap-3 mb-2">
              <motion.h1 
                className="text-2xl lg:text-3xl font-bold text-gray-900"
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: 0.2, duration: 0.5 }}
              >
                {voteTitle}
              </motion.h1>
              <Badge 
                variant={
                  voteStatus === 'ongoing' ? 'success' :
                  voteStatus === 'upcoming' ? 'warning' : 'default'
                }
                size="sm"
              >
                {voteStatus === 'ongoing' ? 'ÏßÑÌñâ Ï§ë' :
                 voteStatus === 'upcoming' ? 'ÏòàÏ†ï' : 'Ï¢ÖÎ£å'}
              </Badge>
            </div>
            
            <motion.div 
              className="flex flex-col sm:flex-row sm:items-center gap-2 text-sm text-gray-600"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 0.3, duration: 0.5 }}
            >
              <span>üìÖ {formatVotePeriod()}</span>
              <span className="hidden sm:inline">‚Ä¢</span>
              <span>üë• Ï¥ù <AnimatedCount value={totalVotes} /> Ìëú</span>
              <span className="hidden sm:inline">‚Ä¢</span>
              <span>üèÜ {filteredItems.length}Î™Ö Ï∞∏Ïó¨</span>
            </motion.div>
          </div>

          {/* ÌÉÄÏù¥Î®∏ & Ïã§ÏãúÍ∞Ñ ÏÉÅÌÉú */}
          <div className="flex flex-col sm:flex-row items-start sm:items-center gap-3">
            {renderTimer()}
            <RealtimeStatus 
              voteId={vote.id}
              compact={true}
            />
          </div>
        </div>

        {/* Í≤ÄÏÉâ */}
        <motion.div 
          className="w-full"
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.4, duration: 0.5 }}
        >
          <VoteSearch 
            onSearch={handleSearch}
            placeholder={`${rankedVoteItems.length}Î™Ö Ï§ë Í≤ÄÏÉâ...`}
            totalItems={rankedVoteItems.length}
          />
        </motion.div>
      </motion.div>

      {/* Ïã§ÏãúÍ∞Ñ Ìà¨Ìëú Í≤∞Í≥º */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.5, duration: 0.5 }}
      >
        <OptimizedRealtimeVoteResults 
          voteId={vote.id}
          showDebugInfo={process.env.NODE_ENV === 'development'}
          enablePerformanceMonitoring={process.env.NODE_ENV === 'development'}
        />
      </motion.div>

      {/* Ìà¨Ìëú ÌôïÏù∏ Î™®Îã¨ */}
      <AnimatePresence>
        {showVoteModal && voteCandidate && (
          <motion.div 
            className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={cancelVote}
          >
            <motion.div 
              className="bg-white rounded-lg p-6 m-4 max-w-md w-full"
              initial={{ scale: 0.9, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.9, opacity: 0 }}
              onClick={(e) => e.stopPropagation()}
            >
              <h3 className="text-lg font-semibold mb-4">Ìà¨Ìëú ÌôïÏù∏</h3>
              
              <div className="mb-4">
                <p className="text-gray-600 mb-2">
                  {voteCandidate.artist?.name ? 
                    getLocalizedString(voteCandidate.artist.name, currentLanguage) :
                    'Ïïå Ïàò ÏóÜÎäî ÏïÑÌã∞Ïä§Ìä∏'
                  }ÏóêÍ≤å Ìà¨ÌëúÌïòÏãúÍ≤†ÏäµÎãàÍπå?
                </p>
                
                <div className="flex items-center gap-2 mb-2">
                  <label className="text-sm font-medium">Ìà¨Ìëú Ïàò:</label>
                  <input 
                    type="number"
                    min="1"
                    max={availableVotes}
                    value={voteAmount}
                    onChange={(e) => setVoteAmount(Math.min(availableVotes, Math.max(1, parseInt(e.target.value) || 1)))}
                    className="border rounded px-2 py-1 w-20 text-center"
                  />
                </div>
                
                <p className="text-xs text-gray-500">
                  ÏÇ¨Ïö© Í∞ÄÎä•Ìïú Ìà¨Ìëú: {availableVotes}Ìëú
                </p>
              </div>

              <div className="flex gap-3">
                <button 
                  onClick={cancelVote}
                  className="flex-1 px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
                >
                  Ï∑®ÏÜå
                </button>
                <button 
                  onClick={confirmVote}
                  disabled={isVoting || voteAmount > availableVotes}
                  className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                >
                  {isVoting ? 'Ìà¨Ìëú Ï§ë...' : 'Ìà¨ÌëúÌïòÍ∏∞'}
                </button>
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Î¶¨ÏõåÎìú ÏÑπÏÖò (ÏûàÎäî Í≤ΩÏö∞) */}
      {rewards.length > 0 && (
        <motion.section 
          className="mt-8"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.6, duration: 0.5 }}
        >
          <h2 className="text-xl font-semibold mb-4">üéÅ Ìà¨Ìëú Î¶¨ÏõåÎìú</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {rewards.map((reward, index) => (
              <motion.div 
                key={reward.id || index}
                className="border rounded-lg p-4"
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.7 + index * 0.1, duration: 0.5 }}
              >
                {/* TODO: Î¶¨ÏõåÎìú Ïπ¥Îìú Íµ¨ÌòÑ */}
                <p>Î¶¨ÏõåÎìú #{index + 1}</p>
              </motion.div>
            ))}
          </div>
        </motion.section>
      )}

      {/* Ïã§ÏãúÍ∞Ñ ÏÉÅÌÉú Ï†ïÎ≥¥ (Í∞úÎ∞ú Î™®Îìú) */}
      {process.env.NODE_ENV === 'development' && (
        <motion.div 
          className="mt-8 p-4 bg-gray-50 rounded-lg border"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.8, duration: 0.5 }}
        >
          <h3 className="font-semibold mb-2">üîß Í∞úÎ∞úÏûê Ï†ïÎ≥¥</h3>
          <div className="text-sm space-y-1 text-gray-600">
            <p>Ïó∞Í≤∞ ÏÉÅÌÉú: {isConnected ? 'üü¢ Ïó∞Í≤∞Îê®' : 'üî¥ Ïó∞Í≤∞ ÏïàÎê®'}</p>
            <p>Ïù¥Î≤§Ìä∏ Ïàò: {eventCount}</p>
            <p>ÎßàÏßÄÎßâ Ïù¥Î≤§Ìä∏: {lastEvent ? new Date().toLocaleTimeString() : 'None'}</p>
            <p>Ïó∞Í≤∞ ÏÉÅÌÉú: {connectionStatus}</p>
            {!isConnected && (
              <button 
                onClick={connect}
                className="mt-2 px-3 py-1 bg-blue-500 text-white rounded text-xs hover:bg-blue-600"
              >
                ÏàòÎèô Ïû¨Ïó∞Í≤∞
              </button>
            )}
          </div>
        </motion.div>
      )}
    </div>
  );
} 